<template>
<div id="wrapper">
<div id="detail__page">
  <div class="detail-pic" v-if="productInfo.data().image == ''" style="background-image: url('https://firebasestorage.googleapis.com/v0/b/vue-project01-a89a2.appspot.com/o/image%2Fno-image-found-360x250-1.png?alt=media&token=2f810a2c-e1ad-4125-b275-c5480eeb55eb');color:white;">등록된 이미지가 없습니다</div>
  <div class="detail-pic" v-else :style="{backgroundImage : `url(${productInfo.data().image})`}"></div>
  <div class="detail__text">
    <h4>작성자 : {{productInfo.data().name}}</h4>
    <hr>
    <h4 class="title">{{productInfo.data().title}}</h4>
    <p class="date">{{productInfo.data().date.toDate().toLocaleString()}}</p>
    <p class="price">가격 : {{productInfo.data().price}}원</p>
    <div>내용 : {{ productInfo.data().detail }}</div>
  </div>
  <div class="detail__buttons">
    <button :disabled="btnEdit">수정</button>
    <button :disabled="btnDel">삭제</button>
    <button :disabled="btnChat">채팅문의</button>
  </div>
</div>

</div>
</template>

<script>
export default {
  name : 'DetailPage',
  data() {
    return {
      btnEdit : true,
      btnDel : true,
      btnChat : true,
      paramId : '',
      db : this.$store.state.db,
      loginUserInfo : localStorage.getItem('user'),
      productInfo : '',

    }
  },
  beforeMount() {
    var querystring = new URLSearchParams(window.location.search);
    this.paramId = querystring.get('id');
    
    this.db.collection('product').doc(this.paramId).get()
    .then((result)=>{
      this.productInfo = result;

      //버튼 권한 설정
      if(JSON.parse(this.loginUserInfo).uid == result.data().uid){
        this.btnChat = true;
        this.btnDel = false;
        this.btnEdit = false;
      }else if(JSON.parse(this.loginUserInfo).email == 'admin@admin.com'){
        this.btnChat = false;
        this.btnDel = false;
        this.btnEdit = false;
      }


    })
  },
}
</script>

<style scoped>
  :root{
    --text-color:#f0f4f5;
    --background-color : #263343;
    --accent-color : #d49466;
  }
  #wrapper{
    display: flex;
    justify-content: center;

  }
  #detail__page{
    width: 700px;
    padding-top: 50px;
    max-width: 700px;
  }
  .detail-pic {
  width: 100%;
  height: 300px;
  background-size: cover;
  background-position: center;
  } 
  .detail__text{
    padding:10px;
  }
  .textarea_field {
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 4px;
    margin: 3px 0;
    font-size: 16px;
    width: 650px;
    height: 160px;
    font-family: 'NanumBarunGothic';
}

.detail__buttons{
  margin : 10px;
}

@media screen and (max-width: 768px) {
  #detail__page{
  max-width: 100%;
  padding-left: 0px;

  }
}
</style>